import React from 'react';
import QueueAnim from 'rc-queue-anim';
import { injectIntl } from 'react-intl';

import './index.css';

import FreeUser from '../../components/FreeUser';

const ProcessComponent = props => {
  const {
    intl: { messages },
  } = props;
  return (
    <QueueAnim type="bottom" className="ui-animate">
      <div key="1" className="row justify-content-md-center">
        <div className="form-card__body col-md-6 p-4">
          <article className="article">
            <div className="box box-default">
              <div className="box-body p-4">
                <section className="form-v1-container">
                  <h2> {messages.process.processAFile}</h2>
                  <p className="lead col-lg-10 mx-lg-auto">{messages.process.subProcessAFile}</p>
                  <p>(docx | xlsx | pptx | pdf | odt | odp)</p>
                  <FreeUser />
                </section>
              </div>
            </div>
          </article>
        </div>
      </div>
    </QueueAnim>
  );
};
export default injectIntl(ProcessComponent);
