/* eslint-disable react-hooks/exhaustive-deps */
import React from 'react';
import { connect } from 'react-redux';
import { injectIntl } from 'react-intl';
import { bindActionCreators } from 'redux';
import ImageLoader from 'react-loading-image';
import styled from 'styled-components';
import { withRouter } from 'react-router-dom';
import {
  Row,
  Col,
  Upload,
  Divider,
  message,
  Form,
  Input,
  Button,
  Select,
  Icon,
  Badge,
  Spin,
} from 'antd';

import * as processActions from 'reducers/process';
import { sendFile, getApiEngines, getProcessByListId } from './../../services';
import functions from './../../utils/functions';
import { fadeIn } from 'styles/animation';
import './index.css';

const countries = require('./../../utils/countries').default;
const Dragger = Upload.Dragger;
const FormItem = Form.Item;
const Option = Select.Option;

const FormItemWrapper = styled(FormItem)`
  margin-bottom: 5px !important;
  & {
    .ant-form-item-label {
      line-height: 20px;
    }
    .ant-form-explain {
      color: #0f416a;
    }
  }
`;

const Div = styled.div`
  width: 100%;
  ${props => (props.error ? `  border: 2px dashed #ea2020` : null)};
`;
const Loading = styled.div`
  max-width: 100%;
  max-height: 100%;
`;

const DivButtons = styled.div`
  display: flex;
  justify-content: center;
`;

const IL = styled(ImageLoader)`
  ${fadeIn()}
  max-width: 100%;
  height: auto;
`;

const jsUcfirst = string => {
  return string.charAt(0).toUpperCase() + string.slice(1);
};

class Process extends React.Component {
  state = {
    allowedFiles: '',
    showBilling: '0',
    secret: null,
    process: [],
    fileList: [],
    uploading: false,
    visible: false,
    payVisible: false,
    processEnd: false,
    showError: false,
    source: null,
    target: null,
    viewNoty: 'process',
    loading: false,
    engines: [],
    defaultValue: [],
    defaultSourceValue: null,
    defaultTargetValue: null,
    selected: {
      engines: [],
    },
    formValues: {
      name: '',
      address: '',
      country: 'ES',
      vattax: '',
    },
    current: 0,
    processSelected: null,
    loadingPay: false,
    applyTax: false,
  };

  componentDidMount = () => {
    this.getEngines();
  };

  getEngines = async () => {
    this.setState({
      loading: true,
    });
    try {
      const engines = await getApiEngines();
      if (engines.data.process && engines.data.process.length) {
        this.setState({
          engines: engines.data.process,
          allowedFiles: engines.data.allowedFiles,
          defaultValue: jsUcfirst(engines.data.value),
        });
        this.initSelected(engines.data.value, engines.data.process);
      }

      this.setState({
        loading: false,
      });
    } catch (err) {
      this.setState({
        loading: false,
      });
    }
  };

  getProcess = async () => {
    const self = this;
    const {
      history,
      processAct: { setProcessSelected },
    } = this.props;
    const result = setInterval(async () => {
      const aux = await getProcessByListId({
        secret: self.state.secret,
      });
      let count = 0;
      if (aux.data.length && aux.data[0].status === 'quote_ready') {
        setProcessSelected(aux.data[0]);
        self.setState({
          process: aux.data,
          payVisible: true,
        });
        history.push('/main/process/pay');
        clearInterval(result);
      }
      if (count > 60) {
        history.push('/main');
        clearInterval(result);
      }
      count++;
    }, 1000);
  };

  handleSubmit = e => {
    e.preventDefault();
    const {
      intl: { messages },
    } = this.props;
    const { target, fileList, selected } = this.state;
    const targets = this.getTargets();

    // eslint-disable-next-line array-callback-return
    const nameEng = targets.filter(item => {
      if (item && item.key === target) {
        return item;
      }
    })[0];

    if (nameEng) {
      this.setState({
        target,
        showError: false,
        viewNoty: 'process',
        current: 0,
        formValues: {
          name: '',
          address: '',
          country: 'ES',
          vattax: '',
        },
        applyTax: false,
      });
    }

    this.props.form.validateFields((err, values) => {
      if (!err) {
        if (fileList.length) {
          const promises = fileList.map(item => {
            return functions.getBase64(item.originFileObj);
          });

          const secret = Math.random()
            .toString(36)
            .slice(-8);
          this.setState({ secret });

          Promise.all(promises)
            .then(files => {
              let aux = values.target.split('-');
              let form = {
                secret: secret,
                process: {
                  id: aux[0],
                  name: selected.name,
                },
                engine: nameEng,
                source: {
                  id: aux[0],
                  name: selected.name,
                },
                files: files,
                email: values.email,
              };
              this.setState({
                uploading: true,
              });
              sendFile(form)
                .then(() => {
                  message.success(messages.default.success);
                  this.setState({
                    uploading: false,
                    viewNoty: 'success',
                    fileList: [],
                  });
                  this.getProcess();
                })
                .catch(error => {
                  message.error(error.message);
                  this.setState({
                    uploading: false,
                    viewNoty: 'error',
                  });
                });
            })
            .catch(err => {
              message.error(err.message);
            });
        } else {
          this.setState({
            showError: true,
          });
        }
      }
    });
  };

  handleChange = value => {
    let selected = null;
    let array = [];

    this.state.engines.forEach(element => {
      if (element.name === value.toLowerCase()) {
        this.setState({
          selected: element,
        });
        selected = element;
      }
    });

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source,
            });
          }
        });
        if (array.length) {
          this.setState({
            defaultSourceValue: array[0].name,
            source: array[0].source,
            target: `${array[0].id}-${array[0].target}`,
            defaultTargetValue: this.getTargets(selected.engines, array[0].source)[0].name,
          });
        }
      }
    }
  };

  initSelected = (value, sources) => {
    let selected = null;
    let array = [];
    const self = this;
    for (let i = 0; i < sources.length; i++) {
      if (sources[i].name === value) {
        self.setState({
          selected: sources[i],
        });
        selected = sources[i];
        break;
      }
    }

    if (selected) {
      if (selected.engines && selected.engines.length) {
        selected.engines.forEach(element => {
          let flag = false;
          for (let i = 0; i < array.length; i++) {
            if (array[i].key === element.source) {
              flag = true;
              break;
            }
          }
          if (!flag) {
            let key = element.source;
            array.push({
              key,
              id: element.id,
              name: functions.languages[key],
              target: element.target,
              source: element.source,
            });
          }
        });
        if (array.length) {
          this.setState({
            defaultSourceValue: array[0].name,
            source: array[0].source,
            target: `${array[0].id}-${array[0].target}`,
            defaultTargetValue: this.getTargets(selected.engines, array[0].source)[0].text,
          });
        }
      }
    }
  };

  changeSource = source => {
    this.setState({
      source,
    });
  };

  changeTarget = target => {
    this.setState({
      target,
    });
  };

  getSources = () => {
    const { selected } = this.state;
    let array = [];
    if (selected.engines && selected.engines.length) {
      selected.engines.forEach(element => {
        let flag = false;
        for (let i = 0; i < array.length; i++) {
          if (array[i].key === element.source) {
            flag = true;
            break;
          }
        }
        if (!flag) {
          let key = element.source;
          array.push({ key, name: functions.languages[key] });
        }
      });
    }

    return array;
  };

  getTargets = (engines = null, _source = null) => {
    const { selected, source } = this.state;
    if (!engines) {
      engines = selected.engines;
    }
    if (!_source) {
      _source = source;
    }

    let array = [];
    engines.forEach(element => {
      let flag = false;
      for (let i = 0; i < array.length; i++) {
        if (array[i].key === element.target) {
          flag = true;
          break;
        }
      }

      if (_source) {
        if (!flag && element.source === _source) {
          if (element.domain) {
            element['key'] = `${element.id}-${element.target}`;
            element['text'] = `${functions.languages[element.target]} - ${element.name} - ${
              element.domain
            }`;
          } else {
            element['key'] = `${element.id}-${element.target}`;
            element['text'] = `${functions.languages[element.target]} - ${element.name}`;
          }
          array.push(element);
        }
      }
    });
    return array;
  };

  renderForms = () => {
    const {
      intl: { messages },
    } = this.props;
    const {
      allowedFiles,
      engines,
      loading,
      fileList,
      showError,
      defaultValue,
      source,
      processEnd,
      defaultSourceValue,
      defaultTargetValue,
      selected,
      uploading,
      target,
    } = this.state;

    const {
      form: { getFieldDecorator, getFieldsError },
    } = this.props;

    const formItemLayout = {
      labelCol: {
        xs: { span: 24 },
        sm: { span: 6 },
      },
      wrapperCol: {
        xs: { span: 24 },
        sm: { span: 16 },
      },
    };
    const tailFormItemLayout = {
      wrapperCol: {
        xs: {
          span: 24,
          offset: 0,
        },
        sm: {
          span: 24,
          offset: 0,
        },
      },
    };

    const dprops = {
      beforeUpload: file => {
        const isLt2M = file.size / 1024 / 1024 < 500;
        if (!functions.checkFile(file, allowedFiles)) {
          message.error(messages.process.error.uploadOne.replace('allowedFiles', allowedFiles));
        } else if (!isLt2M) {
          message.error(messages.process.error.uploadTwo);
        }
        return false;
      },
      onChange: info => {
        let aux = [];
        info.fileList.map(item => {
          if (functions.checkFile(item, allowedFiles)) {
            aux.push(item);
          }
          return aux;
        });
        this.setState({
          fileList: aux,
        });
      },
      multiple: false,
      fileList,
      showUploadList: true,
    };
    let options = [];
    if (engines.length) {
      options = engines.map(d => (
        <Option key={d.id} value={d.name.replace(/^\w/, c => c.toUpperCase())}>
          {d.name.replace(/^\w/, c => c.toUpperCase())}
        </Option>
      ));
    }

    return (
      <React.Fragment>
        <Form {...formItemLayout} onSubmit={this.handleSubmit}>
          <Row type="flex" justify="center" align="middle">
            <Col xs={24}>
              <FormItemWrapper {...tailFormItemLayout}>
                <Div error={showError && !fileList.length}>
                  <Badge style={{ cursor: 'pointer' }} count={fileList.length}>
                    <Dragger {...dprops}>
                      <p className="ant-upload-drag-icon">
                        <Icon type="inbox" />
                      </p>
                      <p className="ant-upload-text">{messages.process.drag}</p>
                    </Dragger>
                  </Badge>

                  {showError && !fileList.length ? message.error(messages.process.alert) : null}
                </Div>
              </FormItemWrapper>
            </Col>
            <Col xs={24}>
              <FormItemWrapper
                validateStatus={loading ? 'validating' : ''}
                label={messages.process.process}
                hasFeedback
              >
                {getFieldDecorator('process', {
                  initialValue: defaultValue,
                  disabled: source === null || processEnd,
                  rules: [
                    {
                      required: true,
                      message: messages.process.rules.process.required,
                    },
                  ],
                })(
                  <Select autofocus="true" onChange={this.handleChange}>
                    {options}
                  </Select>
                )}
              </FormItemWrapper>
            </Col>

            <Col xs={24}>
              <FormItemWrapper label={messages.process.source} hasFeedback>
                {getFieldDecorator('source', {
                  initialValue: defaultSourceValue,
                  rules: [
                    {
                      required: true,
                      message: messages.process.rules.source.required,
                    },
                  ],
                })(
                  <Select disabled={!this.getSources().length} onChange={this.changeSource}>
                    {this.getSources().map(element => {
                      return <Option key={element.key}>{element.name}</Option>;
                    })}
                  </Select>
                )}
              </FormItemWrapper>
              <FormItemWrapper label={messages.process.target} hasFeedback>
                {getFieldDecorator('target', {
                  initialValue: defaultTargetValue,
                  rules: [
                    {
                      required: true,
                      message: messages.process.rules.target.required,
                    },
                  ],
                })(
                  <Select onChange={this.changeTarget} disabled={source === null || processEnd}>
                    {this.getTargets().map(element => {
                      return (
                        <Option key={element.key} value={element.key}>
                          {element.text}
                        </Option>
                      );
                    })}
                  </Select>
                )}
              </FormItemWrapper>
              <FormItemWrapper
                label={messages.process.notification}
                hasFeedback
                help={messages.process.notificationHelp}
              >
                {getFieldDecorator('email', {
                  disabled: processEnd,
                  rules: [
                    {
                      type: 'email',
                      message: messages.process.rules.email.type,
                    },
                    {
                      required: true,
                      message: messages.process.rules.email.required,
                    },
                  ],
                })(<Input disabled={!selected.engines.length} />)}
              </FormItemWrapper>
            </Col>
            <Col xs={24}>
              <FormItemWrapper
                style={{
                  display: 'flex',
                  justifyContent: 'center',
                  marginBottom: 0,
                }}
              >
                {!processEnd ? (
                  <Row justify="center" type="flex">
                    <Button
                      style={{ marginTop: 10 }}
                      icon="upload"
                      type="primary"
                      htmlType="submit"
                      size="large"
                      loading={uploading}
                      disabled={
                        !this.getSources().length ||
                        !target ||
                        !source ||
                        !this.props.form.getFieldValue('email') ||
                        !this.props.form.getFieldValue('email').length ||
                        functions.hasErrors(getFieldsError())
                      }
                    >
                      {uploading ? messages.process.uploading : messages.process.uploadingLoad}
                    </Button>
                  </Row>
                ) : null}
              </FormItemWrapper>
            </Col>
          </Row>
        </Form>
      </React.Fragment>
    );
  };

  showListFile = () => {
    if (this.state.fileList.length) {
      this.setState({
        visible: true,
      });
    }
  };

  hideListFile = () => {
    this.setState({
      visible: false,
    });
  };

  setViewNoty = view => {
    this.setState({
      viewNoty: view,
    });
  };

  onRemove = file => {
    const { processEnd, fileList } = this.state;
    if (!processEnd) {
      let aux = [];
      fileList.map(item => {
        if (item.uid !== file.uid) {
          aux.push(item);
        }
        return aux;
      });
      if (!aux.length) this.hideListFile();
      this.setState({
        fileList: aux,
      });
    }
  };

  onRadioChange = e => {
    this.setState({
      showBilling: e.target.value,
    });
  };

  pay = async id => {
    this.setState({
      loadingPay: id,
    });
    document.getElementById(id).submit();
  };

  next() {
    if (this.state.current === 1) {
      const { form } = this.props;
      form.validateFields((err, values) => {
        if (err) {
          return;
        }
        // form.resetFields();
        const current = this.state.current + 1;
        if (countries[values.country].continent === 'EU') {
          values.continent = countries[values.country].continent;
          this.setState({ current, formValues: values, applyTax: true });
        } else {
          values.continent = countries[values.country].continent;
          this.setState({ current, formValues: values, applyTax: true });
        }
      });
    } else {
      let sum = 1;
      if (this.state.current === 0 && this.state.showBilling === '2') {
        sum = 2;
      }
      const current = this.state.current + sum;
      this.setState({ current });
    }
  }

  prev() {
    let rest = 1;
    if (this.state.current === 2 && this.state.showBilling === '2') {
      rest = 2;
    }
    const current = this.state.current - rest;
    this.setState({ current });
  }

  calculatePrice = price => {
    const { applyTax } = this.state;

    let aux = price.toFixed(2);
    if (aux === '0.00') aux = '0.01';

    if (applyTax) {
      const tax = ((parseFloat(aux) * 21) / 100).toFixed(2);
      aux = parseFloat(aux) + parseFloat(tax);
    }
    return aux;
  };

  handleChange = value => {
    let aux = this.state.formValues;
    aux.country = value;
    this.setState({
      formValues: aux,
    });
  };

  render() {
    const {
      intl: { messages },
    } = this.props;
    const { viewNoty } = this.state;
    if (viewNoty === 'process') {
      return this.renderForms();
    }
    if (viewNoty === 'error') {
      return (
        <div style={{ justifyContent: 'center' }} className="row justify-content-md-center">
          <div className="col-sm-8 col-8 p-4">
            <IL
              src={`${process.env.PUBLIC_URL}/images/undraw_bug_fixing_oc7a.svg`}
              loading={() => <div>{messages.process.loading}...</div>}
              error={() => <div>Error</div>}
            />
            <h2>{messages.process.errorProcess}</h2>
            <p>{messages.process.errorProcessSub}</p>
            <Divider />
            <DivButtons>
              <Button
                onClick={() => {
                  this.setViewNoty('process');
                }}
                type="primary"
                size="large"
              >
                {messages.process.tryAgain}
              </Button>
            </DivButtons>
          </div>
        </div>
      );
    }
    if (viewNoty === 'success') {
      return (
        <div style={{ justifyContent: 'center' }} className="row justify-content-md-center">
          <div className="col-sm-8 col-8 p-4">
            <IL
              src={`${process.env.PUBLIC_URL}/images/undraw_confirmation_2uy0.svg`}
              loading={() => (
                <div>
                  <Loading></Loading>
                </div>
              )}
              error={() => <div>Error</div>}
            />
            <h2>
              <Spin size="large" /> &nbsp; {messages.process.processing}
            </h2>
          </div>
        </div>
      );
    }
  }
}

function mapDispatchToProps(dispatch) {
  return {
    processAct: bindActionCreators(processActions, dispatch),
  };
}

function mapStateToProps(state) {
  return {
    process: state.rootReducer.process,
  };
}

export default connect(
  mapStateToProps,
  mapDispatchToProps
)(Form.create()(injectIntl(withRouter(Process))));
